html,
body, 
#root,
#root > div, 
.app-container {
  height: 100%;
  width: 100%;
  position: relative;
}

.prescreen,
.hat {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  cursor: pointer;
}

.prescreen {
  opacity: 1;
  background: url(./prescreen.jpg) no-repeat center center fixed;
  background-size: cover;
  -webkit-transition: opacity 1.5s ease-in-out;
  -o-transition: opacity 1.5s ease-in-out;
  transition: opacity 1.5s ease-in-out;
}

.prescreen.hidden {
  -webkit-transition: opacity 1.5s ease-in-out;
  -o-transition: opacity 1.5s ease-in-out;
  transition: opacity 1.5s ease-in-out;
  opacity: 0;
}

.hat {
  background: url(./hat.jpg) no-repeat center center fixed;
  background-size: cover;
}




